/* General form container styling */
.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #121212;
  position: relative;
  padding: 20px;
}

.form-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('https://your-image-url.jpg') no-repeat center center;
  background-size: cover;
  filter: blur(5px);
}

.form-content {
  position: relative;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 40px;
  border-radius: 10px;
  width: 450px;
  max-width: 100%;
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

h2 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 28px; /* Increased font size */
  font-weight: 600;
  color: #ff9700;
}

/* Form field styling */
.form-field {
  margin-bottom: 25px; /* Increased margin for better spacing */
}

.form-field label {
  display: block;
  margin-bottom: 8px; /* Increased space between label and input */
  font-size: 16px; /* Increased label font size */
  color: white;
}

/* Input fields */
.input-field {
  width: 100%;
  padding: 14px; /* Increased padding for better touch targets */
  font-size: 16px; /* Larger font size for better readability */
  border-radius: 8px;
  background-color: #1e1e1e;
  color: white;
  border: 1px solid #444;
  transition: border-color 0.3s ease;
}

.input-field:focus {
  border-color: #ff9700;
  outline: none;
}

/* Error styling for input fields */
.input-error {
  border: 1px solid red;
  background-color: #f8d7da; /* Light red background */
}

/* Error messages */
.error-message {
  color: red;
  font-size: 14px; /* Larger error message font */
  margin-top: 5px;
}

/* Checkbox and text should be displayed inline */
.checkbox-container {
  display: flex;
  align-items: center;
  margin-top: 15px;
}

.checkbox-policy input {
  margin-right: 15px;
}

/* Submit and Next buttons */
button[type="submit"],
button[type="button"] {
  width: 100%;
  padding: 15px; /* Increased button padding */
  font-size: 18px; /* Larger font size */
  color: white;
  background-color: #ff9700;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button[type="submit"]:hover,
button[type="button"]:hover {
  background-color: #ff7a00;
  transform: translateY(-2px); /* Slight hover effect */
}

button[type="submit"]:active,
button[type="button"]:active {
  background-color: #e66f00;
  transform: translateY(0); /* Active button effect */
}

button[type="button"]:focus,
button[type="submit"]:focus {
  outline: none;
}

/* Error Button styling (no background, red text) */
.input-error-btn {
  width: 100%;
  padding: 14px;
  font-size: 18px;
  color: red;
  background-color: transparent;
  border: 1px solid red;
  cursor: pointer;
  margin-top: 10px;
  text-align: center;
}

.input-error-btn:hover {
  background-color: #f8d7da; /* Light red background on hover */
}

.input-error-btn:active {
  background-color: #f8d7da; /* Light red background when clicked */
}

.input-error-btn:focus {
  outline: none;
}

/* Login link styling */
.login-link {
  text-align: center;
  margin-top: 25px;
}

.login-link a {
  color: #ff9700;
  text-decoration: none;
  font-size: 16px; /* Increased font size */
}

.login-link a:hover {
  text-decoration: underline;
}

/* Styles for the currency dropdown */
.currency-dropdown {
  position: relative;
}

.currency-btn {
  width: 100%;
  padding: 14px;
  background-color: #1e1e1e;
  color: white;
  border: 1px solid #444;
  border-radius: 8px;
  text-align: left;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.currency-menu {
  position: absolute;
  top: 45px;
  width: 100%;
  background-color: #1e1e1e;
  border: 1px solid #444;
  border-radius: 8px;
  z-index: 100;
}

.currency-option {
  padding: 14px;
  color: white;
  cursor: pointer;
}

.currency-option:hover {
  background-color: #ff9700;
}

.arrow {
  font-size: 16px;
}

/* Styles for ReCAPTCHA */
.recaptcha-container {
  margin-top: 25px; /* Increased space */
  text-align: center;
}

.recaptcha-container .g-recaptcha {
  display: inline-block;
}

/* OTP Inputs */
.otp-inputs {
  display: flex;
  justify-content: space-between;
  gap: 15px; /* Increased gap between OTP fields */
}

.otp-field {
  width: 50px; /* Increased width */
  height: 50px; /* Increased height */
  text-align: center;
  font-size: 22px; /* Larger font size */
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease;
}

.otp-field:focus {
  border-color: #ff9700;
}

.otp-field:focus-visible {
  outline: none;
}

/* Styles for error messages */
.error-message {
  color: red;
  font-size: 14px; /* Larger font size for error messages */
  margin-top: 5px;
}
