/* Header styling */
.header {
  background-color: #121212;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-content {
  display: flex;
  align-items: center;
}

.language-selector {
  display: flex;
  align-items: center;
  gap: 10px;
}

.language-button {
  background-color: #1e1e1e;
  color: white;
  border: 1px solid #ff9700;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
  display: flex;
  gap: 10px;
}

.language-button:hover {
  background-color: #ff9700;
}

.language-button.active {
  background-color: #ff9700;
}

.language-button span {
  margin-left: 10px;
}

.flag {
  width: 20px;
  height: 15px;
  object-fit: contain;
}

.currency-selector {
  margin-left: 20px;
  display: flex;
  align-items: center;
}

.currency-dropdown {
  background-color: #1e1e1e;
  color: white;
  border: 1px solid #ff9700;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.currency-dropdown:focus {
  background-color: #ff9700;
}

.currency-dropdown option {
  background-color: #1e1e1e;
  color: white;
}
